import React, { Component } from 'react';

class Counter extends Component {

    

    state = {
        number : 1
    }

    
    
    render() {
        
        const {number} = this.state;
        console.log({number})
        return (
          <div>
            <h1>Counter using state</h1>
            <p>
            <button onClick={ () => {
                  this.setState({ number: number - 1 })
              } }  > - </button>
              <span> {number} </span>
              <button onClick={ () => {
                  this.setState({ number: number + 1 })
              } }  > + </button>
            </p>
          </div>
        );
    }
}

export default Counter;